import {} from '../configs/app-configs'
import HttpClient from "../utility/HttpClient";

export class AuthService {
  static login = (user) => {
    return HttpClient.post('', user)
  }

  static checkLoginByToken = (user) => {
    return HttpClient.post('', user)
  }

  static getUserInfo = (username, authToken) => {
    const headers = {Authorization: `Bearer ${authToken}`}
    return HttpClient.get(`${''}/${username}`, {headers})
  }

  static getUserByRegisterToken = (registerToken) => {
    return HttpClient.get(`${''}/${registerToken}`)
  }

  static compeleteInfo = (user) => {
    return HttpClient.post(`${''}`, user)
  }

  static logout = (user) => {
    return HttpClient.post(`${''}`, user)
  }

}
