import React from 'react'
import {IMAGE} from "../../configs/app-configs";
import {FormattedMessage} from "react-intl";
import {Link} from "react-router-dom";
import {Button, Col, Row} from "reactstrap";
import {Facebook} from "react-feather";

const LandingPageHeader = ({activeTab}) => {


  const renderMessage = () => {
    switch (activeTab) {
      case 'sign-in' :
        return <div>
          <p>
            <FormattedMessage id={`landingPage.header.loginMessage`}/>
          </p>
          <p>
            <FormattedMessage id={`landingPage.header.notMember`}/> <Link to={'/sign-up'}><FormattedMessage
            id={`landingPage.header.signupNow`}/></Link>
          </p>
        </div>
      case 'sign-up':
        return <div>
          <p>
            <FormattedMessage id={`landingPage.header.alreadyMember`}/> <Link to={'/sign-in'}><FormattedMessage
            id={`landingPage.header.signIn`}/></Link>
          </p>
        </div>
      default :
        return ''
    }
  }

  return (
    <div className='landing-page-header'>
      <div className='ml-3'>
        <img src={IMAGE.LOGO} alt='logo'/>
      </div>
      <div>
        <h2 className='text-primary mt-3' style={{whiteSpace: 'break-spaces'}}>
          <FormattedMessage id='landingPage.slogan'/>
        </h2>
        <div className='mt-3'>
          {renderMessage()}
        </div>
        <Row className='mt-2 d-flex align-items-center'>
          <Col xs={6} md={9}>
            <Button color='primary' className='w-100'>
              <div className='d-flex align-items-center justify-content-center'>
                <img src={IMAGE.GOOGLE_ICON} alt='Google Icon' className='img-fluid'/>
                <span className='ml-1 d-none d-md-block'>
                 <FormattedMessage id='landingPage.header.signInWithGoogle'/>
              </span>
              </div>
            </Button>
          </Col>
          <Col xs={6} md={2}>
            <Button>
              <Facebook size={18}/>
            </Button>
          </Col>
        </Row>
      </div>
      <Row className='mt-2 d-flex align-items-center m-0'>
        <Col xs={4} md={5} className='border-bottom'/>
        <Col xs={4} md={2} className='text-center'><p className='m-0'><FormattedMessage id='common.or'/></p></Col>
        <Col xs={4} md={5} className='border-bottom'/>
      </Row>
    </div>
  )
}

export default LandingPageHeader;
