import getNavigationConfig from '../../../configs/navigationConfig'
import {NavBarService} from '../../../services/navbarService'
import {history} from '../../../history'

export const LOAD_NAVIGATION = 'LOAD_NAVIGATION'
export const LOAD_USER_ROLE = 'LOAD_USER_ROLE'
export const BACK_HOME = 'BACK_HOME'

export const loadNavigation = (appId, navigationConfig) => {
  return async (dispatch) => {
    let roles = [], navConfigs;
    if (!navigationConfig) {
      const res = await NavBarService.getNavigation()
      roles = res.data || []
      navConfigs = getNavigationConfig(appId, roles)
    } else {
      navConfigs = [...navigationConfig];
    }
    dispatch({
      type: LOAD_NAVIGATION,
      payload: {navConfigs, roles}
    })
  }
}

export const loadUserRoles = () => {
  return async (dispatch, getState) => {
    const {groupId} = getState().auth.user
    if (!groupId) {
      return
    }
    const res = await NavBarService.getUserGroupRole(groupId)
    if (res.status === 200) {
      dispatch({
        type: LOAD_USER_ROLE,
        payload: res.data
      })
    }
  }
}

export const goBackHomePage = () => {
  return async (dispatch, getState) => {
    history.push('/')
  }
}
