import React, {useEffect, useState} from 'react'
import '../../assets/scss/pages/authentication.scss'
import {IMAGE} from '../../configs/app-configs'
import Login from './Login'
import {useHistory} from 'react-router-dom'
import Register from './Register'
import styled from 'styled-components'
import {Card, CardBody, Col, Row} from "reactstrap";
import LandingPageHeader from "./LandingPageHeader";


const PageStyle = styled.div`
  height: 100%;

  .landing-page {
    background-image: white;
  }
`

const LandingPage = (props) => {
  const [activeTab, setActiveTab] = useState('')
  const history = useHistory()

  useEffect(() => {
    setActiveTab(props.activeTab || 'sign-in')
  }, [props.activeTab])

  const TabView = () => {
    switch (activeTab) {
      case 'sign-in':
        return <Login/>
      case 'sign-up':
        return <Register/>
      default:
        return ''
    }
  }

  const goToLink = (link) => history.push(link)

  return (
    <PageStyle>
      <div className='landing-page p-2'>
        <Col sm={12} md={7} className='mx-auto mt-3'>
          <Card>
            <CardBody className='p-0'>
              <Row>
                <Col className='mx-auto' sm={12} md={10}  xl={6}>
                  <div className='p-3'>
                    <LandingPageHeader activeTab={activeTab}/>
                    <div className='mt-3'>
                      <TabView/>
                    </div>
                  </div>
                </Col>
                <Col md={6} className='d-none d-xl-block pl-0'>
                  <div className='position-relative'>
                    <div className='bg-overlay'/>
                    <img className='img-fluid' src={IMAGE.LANDING_PAGE_BG} alt={'kid-bg'}/>
                  </div>
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </div>
    </PageStyle>
  )
}

export default LandingPage
