import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { useDispatch, useSelector } from 'react-redux'
import { Button } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import { loginAction } from '../../redux/actions/auth/index'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import {
  PASSWORD_REGEX,
  REMEMBER_ME_TOKEN
} from '../../configs/app-configs'
import BaseFormGroup from '../../components/BaseFormGroup.js'
import { trimValue } from '../../utility'
import {history} from "../../history";

const formSchema = Yup.object().shape({
  username: Yup.string().required(
    <FormattedMessage id='login.username.required' />
  ),
  password: Yup.string()
    .required(<FormattedMessage id='login.password.required' />)
    .matches(PASSWORD_REGEX, () => (
      <FormattedMessage id='login.password.invalid' />
    ))
})
const Login = () => {
  const [rememberMe, setRememberMe] = useState(null)
  const [isRemeberMe, setIsRemeberMe] = useState(false)
  const dispatch = useDispatch()

  const loginStatus = useSelector((state) => state.auth.loginStatus)

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem(REMEMBER_ME_TOKEN))
    if (user) {
      setRememberMe(user)
    }
  }, [])

  const onSubmit = (values, actions) => {
    dispatch(
      loginAction({
        username: trimValue(values.username),
        password: values.password,
        isRemeberMe
      })
    )
    actions.setSubmitting(false)
  }

  const onClickNotMe = () => {
    localStorage.removeItem(REMEMBER_ME_TOKEN)
    setRememberMe(null)
  }

  return (
    <Formik
      enableReinitialize
      initialValues={{
        username: 'admin',
        password: 'Admin12345'
      }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form>
          <BaseFormGroup fieldName='username' messageId='login.username' errors={errors} touched={touched} />
          <BaseFormGroup fieldName='password' messageId='login.password' errors={errors} touched={touched} type='password' />
          <div className='d-flex'>
            <Button color='primary' type='submit'>
              <FormattedMessage id='login' />
            </Button>
            <Button color='secondary' type='button' onClick={() => history.push('/sign-up')} outline className='ml-2' >
              <FormattedMessage id='signUp' />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default Login
